.works{
    width: 100%;
    
    padding: 0 5px;
    &_wrapper{
        display: flex;
        flex-direction: column;
    }
    &_title{
        font-size: 50px;
        line-height: 140%;
        margin-top: 100px;
    }
    &_content{
        margin-top: 60px;
        
    }

    &_inner{
        position: relative;
        border-radius: 10px;
        
    }
    .works_inner_reverse{
        position: relative;
        border-radius: 10px;
        margin-top: 40px;

       
    }
    &_img{
        width: 60%;
        height: 300px;
        border-radius: 10px;
        border: 2px solid transparent;
        transition: all .3s linear;
        &:hover{
            border: 2px solid rgba(30, 84, 200, 0.649);
            border-radius: 10px;
            transition: all .2s linear;
        }
        
    }
    &_img_reverse{
        display: block;
        margin-left: auto;
        width: 60%;
        height: 300px;
        border-radius: 10px;
        border: 2px solid transparent;
        transition: all .1s linear;
        &:hover{
            border: 2px solid rgba(30, 84, 200, 0.649);
            border-radius: 10px;
            transition: all .2s linear;
        }
        
    }
    &_undertitle{
        text-align: right;
        margin-right: 20px;
        font-size: 14px;
        line-height: 140%;
    }
    &_inner_textbox{
        width: 50%;
        position: absolute;
        top:20px;
        right: 0;
       h3{
        font-size: 29px;
        line-height: 140%;
        text-align: right;
        margin-right: 20px;
        } 
    }
    &_inner_textbox_reverse{
        width: 50%;
        position: absolute;
        top:20px;
        left: 0;
       h3{
        font-size: 29px;
        line-height: 140%;
        text-align: left;
        margin-right: 20px;
        } 
        .works_undertitle{
            text-align-last: left;
        }
    }
    &_list{
        margin-top: 20px;
        display: flex;
        justify-content: center;
        
    }
    &_item:not(:first-child){
        margin-left: 20px;
       
    }
    &_descr{
        color: white;
        background: #434B4D;
        border-radius:10px;
        padding: 10px;
        margin-top: 20px;
    }
    
}


@media (max-width: 700px) {
    .works{
       padding-bottom: 0px;

        &_inner_textbox{
            width: 60%;
            top:5px;
        }
        &_inner_textbox_reverse{
            width: 60%;
            top:5px;
        }
        &_img{
            width: 60%;
            height: 280px;
        }
        &_img_reverse{
            width: 60%;
            height: 280px;
        }
    }   
}
@media (max-width: 640px) {
    .works{
        &_inner_textbox{
         position: relative;  
            width: 96%;
        }
        &_inner_textbox_reverse{
         position: relative;  
            width: 96%;
        }
        &_img{
            width: 96%;
            height: 240px;
        }
        &_img_reverse{
            width: 96%;
            height: 240px;
        }
        &_descr{
            margin-top: 0;
        }
    }
}
