.container{
 padding: 20px;

}
.works{
    
    &_item{
        width: 30%;
        margin-right: 30px;
    }
    &_date{
        font-size: 14px;
    }
    &_box{
      width: 500px;
    }
    &_position{
        font-weight: 700;
        font-size: 18px;
        line-height: 20px;
    }
    &_stack{
        font-size: 14px;
        line-height: 16px;
        margin: 10px 0;
    }
    &__inner{
        display: flex;
        justify-content: space-between;
        margin-top: 50px;
    }
    &__left{
        margin: right 30px; ;
        width: 20%;
    }
    &__content + &__content{
        margin-top: 30px;
    }
 
    &__website{
        text-decoration: underline;
        margin-top: 20px;
        display: block;
        font-size: 14px;
    }
}
@media(max-width: 770px){
    .works{
        &__inner{
            justify-content: flex-start;
        }
        &_box{
            max-width: 350px;
            margin-left: 40px;
        }
        &_descr{
         
    
        }
    }
}
@media (max-width: 565px) {
    .container{
        margin-top: 20px;
        padding: 0;
    }
    .works{
        &_list{
            flex-direction: column;
        }
        &_box{
            max-width: 276px;
            margin-left: 60px;
          }
      
    }
}
@media (max-width: 425px) {
    .works{
        &_list{
            margin-top: 30px;
        }
        &_box{
            max-width: 220px;
            
        }
    }
}



