.footer{
    height: 30px;
    margin-top: 80px;
    padding-bottom: 20px;
    text-align: center;
    font-size: 12px;
    line-height: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.footer__arrow{
    position: absolute;
    top: 0;
    right: 0;
    animation: move 3s linear infinite;
}

@keyframes move {
    0%{
        transform: translateY(-5px);
    }
    50%{
        transform: translateY(5px);
    }
    100%{
        transform: translateY(-5px);
    }
}